@value makerGreen, makerWhite, makerLightPink from '../../common/colors.css';

.login {
	width: 30em;
	text-align: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-color: makerWhite;
}

.list {
	width: 100%;
	display: flex;
	flex-direction: column;
	list-style: none;
	padding: 0.5rem;
	box-sizing: border-box;
}

.item {
	margin-bottom: 0.5em;
}

.button {
	width: 100%;
	height: 2.5em;
	font-size: 1.2rem;
	border-radius: 1.2rem;
	background-color: transparent;
	cursor: pointer;
	border: 0.2rem solid makerGreen;
	outline: 0;
	box-sizing: border-box;
}

.button:hover {
	background-color: makerLightPink;
}